
@import url('https://fonts.googleapis.com/css?family=El+Messiri:600|Roboto:300|Spectral|Roboto|PT+Sans+Narrow&display=swap');
#app{
	font-family: 'Roboto', serif;
	font-weight: 500;
}
/* Окно, если авторизация успешна */
.authenticated {
	height: 100vh;
	width: calc(100vw-15px);
	display: grid;
	grid-template-rows: -webkit-max-content auto -webkit-max-content;
	grid-template-rows: max-content auto max-content;
}
/* Окошко "Система загружается" после успешной авторизации*/
.load{
	height: 100vh;
	width: 100vw;
	display: grid;
	grid-template-rows: auto -webkit-max-content auto;
	grid-template-rows: auto max-content auto;
}
.loadText{
	margin: 0 auto;
	background: #64b2db;
	padding: 10px;
}
.load p{
	color: white;
	padding: 0px;
	text-align: center;
}
.loadSvg{
    fill: white;
    enable-background: new 0 0 16 16;
    padding: 15px;
    margin-left: 15px;
    height: 30px;
    width: 30px;
    -webkit-animation: spin 3s linear infinite;
            animation: spin 3s linear infinite;
}
@-webkit-keyframes spin {
100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
}
}
@keyframes spin {
100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
}
}
/**/
*:not(svg):hover{
	-webkit-transition: all 0.2s;
	transition: all 0.2s;
}
router-view{
	overflow-y: auto;
	height: 100vh;
}
@media (max-width: 500px) {
*{
		font-size: 14px;
}
}
/* Для всего проекта */
button:not(.showPassword), .button{
	border: 0;
	/* border-radius: 5px; */
	padding: 8px;
    margin-top: 15px;
    margin-bottom: 15px;
	margin-right: 7px;
	color: white;
    font-family: 'Courier New', Courier, monospace;
    font-size: 14px;
	background-color: #347090;
  -webkit-box-shadow: 0 3px rgba(170,169,248,1);
          box-shadow: 0 3px rgba(170,169,248,1);
  -webkit-transform: translate(0, -3px);
          transform: translate(0, -3px);
	-webkit-box-shadow: 0 5px 0px 0px rgba(170,169,248,1);
	        box-shadow: 0 5px 0px 0px rgba(170,169,248,1);
}
button:active, a:active{
  -webkit-box-shadow: none;
          box-shadow: none;
  -webkit-transform: translate(0px, 0px);
          transform: translate(0px, 0px);
}
button:hover {

  cursor: pointer;
}
input[type="checkbox"]{
    height: 15px;
}
input{
	border: 0;
	height: 30px;
	margin: 0 auto;
	padding-left: 15px;
	padding-right: 15px;
}
