#adm[data-v-77ea16e0]{
    margin-left: 15px;
    padding: 45px;
    padding-top: 0;
    padding-left: 60px;
}
/* Поле ввода */
input[type="email"][data-v-77ea16e0], input[type="text"][data-v-77ea16e0]{
	border: 0;
	margin: 0;
    height: 35px;
    padding-left: 15px;
	padding-right: 15px;
    min-width: 350px;
    background: rgb(223, 243, 253);
}
/**/
input[type="checkbox"][data-v-77ea16e0]{
    height: 15px;
    width: 15px;
    margin: 0;
    vertical-align: middle;
}
/* Картотеки */
.fileCabinet[data-v-77ea16e0], .reg[data-v-77ea16e0]{
    border: #347090 1px solid;
    border-bottom: 0;
    padding: 5px;
}
.editNameFileCabinet[data-v-77ea16e0], .editNameReg[data-v-77ea16e0]{
    display: grid;
	grid-template-columns: 1fr auto;
}
.editNameFileCabinet2[data-v-77ea16e0], .editNameReg2[data-v-77ea16e0]{
    display: grid;
	grid-template-columns: 1fr auto auto;
}
.addFileCabP[data-v-77ea16e0]:hover, .addUserP[data-v-77ea16e0]:hover, .addRegP[data-v-77ea16e0]:hover{
    cursor: pointer;
}
/**/
table[data-v-77ea16e0] {
    border-collapse: collapse;
    text-align: left;
}
table[data-v-77ea16e0], th[data-v-77ea16e0], td[data-v-77ea16e0]{
    border: #64b2db 2px solid;
    border-radius: 5px;
}
th[data-v-77ea16e0]{
    background: rgb(223, 243, 253);
}
td[data-v-77ea16e0], th[data-v-77ea16e0]{
    padding: 7px 15px;
}
.arrow[data-v-77ea16e0]{
    display: inline-block;
}
.editAdm[data-v-77ea16e0]{
    display: grid;
	grid-template-columns: 1fr auto;
}
.editNotif[data-v-77ea16e0]{
    display: grid;
	grid-template-columns: 1fr auto;
}
.editStaff[data-v-77ea16e0], .editNotif2[data-v-77ea16e0]{
    fill: #64b2db;
    vertical-align: text-bottom;
    text-align: right;
}
.editStaff[data-v-77ea16e0]:hover, .editNotif2[data-v-77ea16e0]:hover{
    cursor: pointer;
    fill: #347090;
}
/**/
hr[data-v-77ea16e0] { 
    overflow: visible;
    border: 0;
    margin-left: -2px;
    border-top: dotted #347090 4px;
}
/**/
canvas[data-v-77ea16e0] {
    border: 1px solid #BBB;
    display: none;
}
