
#profile[data-v-fa776194]{
    height: 100%;
	background: white;
}
#profile > *[data-v-fa776194]{
	padding: 25px 50px;
}
.mainProfile[data-v-fa776194]{
	max-width: 1440px;
	margin: auto;
	display: grid;
	grid-template-columns: -webkit-max-content auto;
	grid-template-columns: max-content auto;
}
/**/
.menuProfile[data-v-fa776194]{
	font-family: 'PT+Sans+Narrow', sans-serif;
	font-weight: 300;
	border: #e0e0e0 3px solid;
	border-radius: 5px;
}
.router-link[data-v-fa776194]{
	padding: 10px 20px;
	margin-top: 5px;
	margin-bottom: 5px;
	text-decoration: none;
	color: black;
	width: calc(100% - 40px);
	margin-left: 0;
	margin-right: 0;
	display: block;
}
.router-link[data-v-fa776194]:hover{
	cursor: pointer;
	color: #7cb0c1;
}
/**/
.router-link-exact-active[data-v-fa776194]{
	background: rgb(223, 243, 253);
}
/**/
.openCloseMenuButton[data-v-fa776194]{
    margin: 5px;
}
.openCloseMenuButton[data-v-fa776194]:hover, .openCloseMenuButton:hover *[data-v-fa776194]{
    cursor: pointer;
	color: #7cb0c1;
	fill: #7cb0c1;
}
@media (max-width: 500px) {
.mainProfile[data-v-fa776194]{
		grid-template-columns: auto;
		grid-template-rows: -webkit-min-content auto;
		grid-template-rows: min-content auto;
		padding: 0;
}
}
