
#addDoc[data-v-d4a2f00c]{
    height: 100%;
	width: 100%;
    max-width: 900px;
    margin: 0 auto;
	background: white;
    padding: 25px;
    padding-bottom: 0;
}
.header[data-v-d4a2f00c]{
    margin-top: 0;
	margin-bottom: 30px;
}
/* Поля ввода */
.filename[data-v-d4a2f00c], textarea[data-v-d4a2f00c]{
	border: 0;
	margin: 0 auto;
    padding: 10px;
    font-size: 13.5px;
    min-width: 900px;
    max-width: 900px;
    background: rgb(223, 243, 253);
}
select[data-v-d4a2f00c]{
    margin-right: 10px;
    padding: 6px;
    font-size: 13.5px;
    min-width: 250px;
}
.selectedUser[data-v-d4a2f00c]{
    display:inline-block;
    margin: 0;
    padding:0;
    vertical-align:top;
    font-weight: 400;
}
.selectedUserWithCancel[data-v-d4a2f00c]{
    background: rgb(223, 243, 253);
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 5px;
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-rows: auto;
}
.selectedUserWithCancel[data-v-d4a2f00c]:hover{
    background: rgb(223, 243, 253);
    cursor: pointer;
}
.deleteSelectedUser[data-v-d4a2f00c]{
    padding: 0px;
    padding-left: 5px;
}
/**/
form > p[data-v-d4a2f00c]{
    margin-bottom: 5px;
}
/**/
.addDoc2Colon[data-v-d4a2f00c]{
    display: grid;
    grid-template-columns: -webkit-max-content auto;
    grid-template-columns: max-content auto;
}
/**/
img[data-v-d4a2f00c]{
    width: 110px;
}
/**/
/**/
.disabled[data-v-d4a2f00c]{
    pointer-events: none;
    background: rgb(133, 133, 133);
}
