
#document[data-v-7a60a7a5]{
    height: calc(100% - 50px);
	width: 100%;
    max-width: 900px;
    margin: 0 auto;
	background: white;
    padding: 25px;
}
.header[data-v-7a60a7a5]{
    margin-top: 0;
	margin-bottom: 10px;
}
/**/
.document2Colon[data-v-7a60a7a5]{
    display: grid;
    grid-template-columns: -webkit-max-content auto;
    grid-template-columns: max-content auto;
	margin-top: 20px;
}
/**/
button[data-v-7a60a7a5]{
    margin-top: 5px;
    margin-bottom: 10px;
}
.buttons[data-v-7a60a7a5]{
	max-width: 900px;
}
.signButtons[data-v-7a60a7a5]{
	display: grid;
    grid-template-columns: 1fr auto auto 1fr;
}
.signButtons button[data-v-7a60a7a5]{
	margin: 10px;
	margin-top: 5px;
	margin-bottom: 20px;
}
.confirmButtons[data-v-7a60a7a5]{
	display: grid;
    grid-template-columns: 1fr auto 1fr;
}
.confirmButtons button[data-v-7a60a7a5]{
	margin: 10px;
	margin-top: 25px;
}
a[data-v-7a60a7a5]{
	color: white;
	text-decoration: none;
}
/* Подтверждение */
.confirm[data-v-7a60a7a5], .checkSignature[data-v-7a60a7a5]{
	position: fixed;
	top: 20%;
	left: calc(50% - 200px);
	margin: 0;
	width: 400px;
	background: rgb(223, 243, 253);
	border: 1px solid #347090;
}
.notAddHeight[data-v-7a60a7a5]{
	height: 240px;
}
.addHeight[data-v-7a60a7a5]{
	height: 300px;
}
.confirm p[data-v-7a60a7a5], .checkSignature p[data-v-7a60a7a5]{
	padding: 20px;
	padding-bottom: 10px;
	text-align: justify;
}
.code[data-v-7a60a7a5]{
	border: 0;
	margin: 0 auto;
	margin-left: 20px;
	margin-right: 20px;
    padding: 10px;
    font-size: 13.5px;
    width: calc(100% - 60px);
    background: white;
	color: black;
}
/* Причина отклонения, комментарий */
textarea[data-v-7a60a7a5]{
	border: 0;
	margin: 0 15px;
    padding: 10px;
    font-size: 13.5px;
    min-width: 350px;
	max-width: 350px;
	min-height: 70px;
	max-height: 70px;
    background: white;
}
/* Файл, который загружается при отклонении подписи*/
.cancelFile[data-v-7a60a7a5]{
	margin: 15px;
	margin-bottom: 0;
}
/* Таблица */
table[data-v-7a60a7a5] {
    border-collapse: collapse;
}
.aboutDoc *[data-v-7a60a7a5]{
	max-width: 500px;
	word-wrap: break-word;
}
table[data-v-7a60a7a5], tr[data-v-7a60a7a5]{
    border: white 7px solid;
	border-left: 0;
	border-right: 0;
    border-radius: 5px;
}
td[data-v-7a60a7a5]{
    padding: 7px 15px;
	background: #d5dbdf;
}
