
#grid[data-v-b0676830]{
	width: 100%;
	background: white;
}
table[data-v-b0676830] {
    border-collapse: collapse;
    width: calc(100% - 30px);
    margin:15px;
}
table[data-v-b0676830], th[data-v-b0676830], td[data-v-b0676830]{
    border-bottom: #64b2db 2px solid;
}
td[data-v-b0676830], th[data-v-b0676830]{
    padding: 7px 15px;
}
th[data-v-b0676830]{
    background: rgb(223, 243, 253);
    font-weight: 500;
    text-align: left;
}
.rowData[data-v-b0676830]{
    background: var(--button-background-color);
}
.rowData[data-v-b0676830]:hover{
    cursor: pointer;
    background: #64b2db;
}
.arrow[data-v-b0676830]{
    display: inline-block;
}
/* Поиск */
.search[data-v-b0676830]{
    margin: 35px;
    margin-top: 35px;
    margin-bottom: 20px;
}
/**/
input[data-v-b0676830]{
    border: 0;
    background: rgb(223, 243, 253);
	height: 30px;
	min-width: 250px;
	margin: 0 auto;
	margin-left: 0px;
	padding-left: 15px;
	padding-right: 15px;
}
/* Недоступность документов при обновлении данных */
.disabled[data-v-b0676830]{
    display: none;
}
.commonButton[data-v-b0676830]{
    background-color: #347090;
}
/**/
.closeNotif[data-v-b0676830]{
    margin:auto;
    padding:7px;
}
.closeNotif:hover > *[data-v-b0676830]{
    fill:white;
    stroke: white;
}
@media (max-width: 500px) {
