
















































































































































/* Основной экран */
#mainAuth[data-v-6119e7be]{
	margin: 0 auto;
	height: 100vh;
	display: grid;
    grid-template-columns: auto -webkit-max-content auto;
    grid-template-columns: auto max-content auto;
}
#mainAuth > *[data-v-6119e7be]{
	text-align: center;
}
/* Чтобы посередине окно было */
.login[data-v-6119e7be]{
	display: grid;
	grid-template-rows: 1fr -webkit-max-content 2fr;
	grid-template-rows: 1fr max-content 2fr;
}
/* Заголовок */
.title[data-v-6119e7be]{
	font-size: 26px;
	padding-top: 30px;
	padding-bottom: 8px;
}
/* Параметры окна */
.auth[data-v-6119e7be]{
	width: 400px;
	background: #ADE0FC;
}
/* Кнопка ВОЙТИ, переопределение */
button[data-v-6119e7be]{
    margin-top: 0;
    margin-bottom: 0;
}
/* Кнопка "Забыли пароль?"*/
.rememberPassword[data-v-6119e7be]{
	padding-top: 30px;
	padding-bottom: 30px;
	color: #347090;
	font-size: 14px;
}
.rememberPassword[data-v-6119e7be]:hover{
	cursor: pointer;
}
.showPassword[data-v-6119e7be] {
  padding: 8px 12px 6px 0;
  margin: 0;
  background: 0;
  border: 0;
}
