/**/
.reload[data-v-11e0d1e2]{
    fill: white;
    enable-background: new 0 0 16 16;
    padding: 15px;
    margin-left: 10px;
    height: 20px;
    width: 20px;
    -webkit-animation: spin-data-v-11e0d1e2 2s linear infinite;
            animation: spin-data-v-11e0d1e2 2s linear infinite;
}
@-webkit-keyframes spin-data-v-11e0d1e2 {
100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
}
}
@keyframes spin-data-v-11e0d1e2 {
100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
}
}
